import { useTranslation } from "react-i18next";
import style from "../JoiningForm/JoiningForm.module.css";
import { useHostelApplyApplicationList } from "../optimized/Hooks/ApiHooks/HostelManager/hostel-manager-api";
import Loading from "../optimized/Loader/Loading";

function Card({ applyData, setIndex, setOtherData }) {
  const { t } = useTranslation();
  return (
    <div
      className={style.card}
      onClick={() => {
        setIndex(7);
        setOtherData(applyData);
      }}
    >
      <div className={style.card_row_container}>
        <img
          src="/images/members/admission-application-icon.svg"
          alt="admission icon"
        />
        <div
          style={{
            width: "100%",
          }}
        >
          <div className={style.cardRow}>
            <h6
              style={{
                marginBottom: "0.2rem",
              }}
            >
              {applyData?.applicationName}
            </h6>
            {applyData?.application_type === "Direct Admisison" ? (
              <div className={style.hostel_direct}>
                {applyData?.applicationEndDate ? (
                  <h6
                    style={{
                      marginBottom: "0.2rem",
                      marginRight: "1rem",
                    }}
                  >
                    {applyData?.applicationEndDate}
                  </h6>
                ) : (
                  ""
                )}
                {applyData?.application_type === "Direct Admisison" && (
                  <img
                    src="/images/members/application-hotspot-icon.svg"
                    alt="admission icon"
                  />
                )}
              </div>
            ) : applyData?.applicationEndDate ? (
              <h6
                style={{
                  marginBottom: "0.2rem",
                  marginRight: "2.3rem",
                }}
              >
                {applyData?.applicationEndDate}
              </h6>
            ) : (
              ""
            )}
          </div>
          <div className={style.cardRow}>
            <p>{applyData?.applicationUnit?.hostel_unit_name ?? ""}</p>
            {applyData?.applicationEndDate && (
              <p
                style={{
                  marginRight: "2.3rem",
                }}
              >
                {t("last_date_to_apply")}
              </p>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

function HostelApplyApplication({ instituteId, setIndex, setOtherData }) {
  const { t } = useTranslation();

  const { hostelApplyApplicationList, hostelApplyApplicationListLoading } =
    useHostelApplyApplicationList({
      data: {
        id: instituteId,
        page: 1,
        limit: 1000,
        search: "",
      },
      skip: !instituteId,
    });

  return (
    <div className={style.applications}>
      <div className={style.top}>{t("join_or_apply_instutude")}</div>
      <div className={style.content}>
        <div className={style.contentBottom}>
          <h6>{t("new_application")}</h6>
          <div className={style.cardsContainer}>
            {hostelApplyApplicationList?.list?.map((newApp) => (
              <Card
                key={newApp?._id}
                applyData={newApp}
                setIndex={setIndex}
                setOtherData={setOtherData}
              />
            ))}
            {hostelApplyApplicationListLoading && <Loading />}
          </div>
        </div>
      </div>
    </div>
  );
}

export default HostelApplyApplication;
